#bg_col {
  background-color: white;
}
#app {
  margin: 0px auto;
  height: 900px;
  text-align: center;
  padding-top: 20%;
}
.switch {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  border: 0.2px solid black;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: black;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
input:checked + .slider:before {
  -webkit-transform: translateX(66px);
  -ms-transform: translateX(66px);
  transform: translateX(66px);
}
.slider.round {
  border-radius: 34px;
}
.slider.round:before {
  border-radius: 50%;
}
#text {
  font-size: 2em;
  font-weight: 900;
  font-family: "Times New Roman", Times, serif;
  letter-spacing: 1px;
}
